.up {
  width: 100%;
  height: 2.133333rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  background: white;
  position: fixed;
  bottom: 0;
}

.up .van-button {
  margin-left: 5%;
  width: 90%;
  height: 1.066667rem;
  border-radius: 0.066667rem;
}

.add_bg {
  overflow: hidden;
}

.resume-page {
  width: 100%;
  height: 110vh;
  padding-bottom: 1.6rem;
  position: relative;
  box-sizing: border-box;
  text-align: left;
  overflow: auto;
}

.resume-page .err div {
  width: 4rem;
  height: 5.333333rem;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -2.4rem 0 0 -2rem;
}

.resume-page .err div .iconfont {
  color: orange;
  font-size: 1.306667rem;
}

.resume-page .err div div {
  font-size: 0.4rem;
  color: #211F22;
  margin-top: -1.066667rem;
}

.resume-info {
  height: 100%;
}

.resume-title {
  color: #000000;
  font-size: 0.4rem;
  text-align: center;
  padding: 0.4rem 0;
  font-weight: 500;
}

.wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 100%;
}

.block {
  width: 1.6rem;
  height: 1.6rem;
  background-color: #fff;
}


