.home-page {
  width: 100vw;
  height: 100vh;
  padding: 0.666667rem;
  box-sizing: border-box;
}

.home-page .err div {
  width: 4rem;
  height: 5.333333rem;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -2.4rem 0 0 -2rem;
}

.home-page .err div .iconfont {
  color: orange;
  font-size: 1.306667rem;
}

.home-page .err div div {
  font-size: 0.4rem;
  color: #211F22;
  margin-top: -1.066667rem;
}

.home-page .title {
  font-size: 0.48rem;
  color: #000;
  font-weight: 700;
  margin-top: 0.666667rem;
  word-wrap: break-word;
}

.home-page .subtitle {
  font-size: 0.346667rem;
  color: #000;
  margin: 0.2rem 0 0.666667rem 0;
  word-wrap: break-word;
}

.home-page .mode {
  width: 100%;
  height: 4rem;
  border-radius: 0.533333rem;
  margin-bottom: 0.4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.4rem;
  box-sizing: border-box;
}

.home-page .mode.write {
  border: 0.013333rem solid #e5f4ea;
  background: #e5f4ea;
}

.home-page .mode.upload {
  border: 0.013333rem solid #e7f4ff;
  background: #e7f4ff;
}

.home-page .mode .van-uploader {
  width: 100%;
  height: 100%;
}

.home-page .mode .van-uploader .van-uploader__wrapper {
  width: 100%;
  height: 100%;
}

.home-page .mode .van-uploader .van-uploader__wrapper .van-uploader__input-wrapper {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.home-page .mode img {
  width: 3.466667rem;
  height: 2.933333rem;
}

.home-page .mode .mode-text {
  font-size: 0.533333rem;
  font-weight: 700;
  color: #000;
  margin-left: 0.626667rem;
}

.home-page .msg {
  font-size: 0.293333rem;
  color: #333;
  padding: 0.533333rem 0.706667rem 0.533333rem 0.933333rem;
  line-height: 0.533333rem;
  background: #fff9eb;
  border: 0.013333rem solid #ffc378;
  border-radius: 0.533333rem;
  position: relative;
}

.home-page .msg::before {
  content: "";
  width: 0.4rem;
  height: 0.4rem;
  background: url("../assets/img/attention.png") center center no-repeat;
  background-size: 0.4rem;
  position: absolute;
  top: 0.573333rem;
  left: 0.533333rem;
}

.home-page .logo {
  width: 1.333333rem;
  height: 0.266667rem;
  position: absolute;
  left: 50%;
  bottom: 1.066667rem;
  transform: translateX(-50%);
}

.home-page .logo img {
  width: 100%;
  height: 100%;
}


