.success div {
  width: 4rem;
  height: 5.333333rem;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -2rem 0 0 -2rem;
}

.success div img {
  width: 2.4rem;
  height: 2.4rem;
}

.success div div {
  font-size: 0.4rem;
  color: #211F22;
  margin-top: 0;
}


